import React, { useState } from 'react'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import TinyEditor from '../../components/UI/TinyEditor'

const FolderMM = props => {
    const {
        selectedBox,
        el,
        id,
        editingText,
        setEditingText,
        saveValueMM,
    } = props
    const [textValue, setTextValue] = useState()

    return (
        <div style={{ background: 'white', flex: 1 }}>
            {el.imageValue && (
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: 'center',
                        backgroundImage: 'url("' + el.imageValue + '")',
                    }}
                />
            )}
            {el.textValue && <TinyEditor MM value={el.textValue} readOnly />}
            <div className="tinyEditorText_">
                <DialogFullscreen
                    open={editingText && selectedBox === id}
                    close={() => {
                        setEditingText(false)
                    }}
                    save={() => {
                        saveValueMM(id, { textValue })

                        setEditingText(false)
                    }}
                >
                    <div className="tinyEditorText_">
                        {editingText && selectedBox === id && (
                            <TinyEditor
                                onEditorChange={value => setTextValue(value)}
                                value={el.textValue}
                            />
                        )}
                    </div>
                </DialogFullscreen>
            </div>
        </div>
    )
}

export default FolderMM
