import {
    AppBar,
    createStyles,
    Dialog,
    IconButton,
    makeStyles,
    Slide,
    Theme,
    Toolbar,
    Typography,
    Button,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import React from 'react'
import { useTranslation } from 'react-i18next'

const TransitionComponent = props => <Slide direction="up" {...props} />

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
export default props => {
    const { t } = useTranslation()
    const classes = useStyles()
    return (
        <Dialog
            fullScreen
            open={props.open}
            onClose={props.close}
            TransitionComponent={TransitionComponent}
            scroll="paper"
        >
            <div className="tinyEditorText_">
                <AppBar className={classes.appBar} color="primary">
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={props.close}
                            aria-label="close"
                        >
                            <Close />
                        </IconButton>
                        <Typography
                            variant="h6"
                            className={classes.title}
                            color="inherit"
                        >
                            {props.title}
                        </Typography>
                        {props.save && (
                            <Button
                                autoFocus
                                color="inherit"
                                onClick={props.save}
                            >
                                {props.saveText || t('g.save')}
                            </Button>
                        )}
                    </Toolbar>
                </AppBar>
                <Toolbar />
                {props.children}
            </div>
        </Dialog>
    )
}
