import React, { useEffect, useState } from 'react'

import { TransformComponent } from 'react-zoom-pan-pinch'
import ContainerMM from './ContainerMM'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import * as actions from '../../store/actions'
import 'tui-image-editor/dist/tui-image-editor.css'

import BottomMenu from '../../components/Layout/BottomMenu'
import { Button } from '@material-ui/core'
import { ArrowBack } from '@material-ui/icons'

import Layout from '../../components/Layout/Layout'
import { withRouter, Prompt } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useBeforeunload } from 'react-beforeunload'

const MindMap = props => {
    const { t } = useTranslation()
    const [selectedBox, setSelectedBox] = useState('')

    const [changeParentMode, setChangeParentMode] = useState('')
    const [editorMode, setEditorMode] = useState(false)
    const {
        structure,
        saveStructure,
        account,
        profile,
        setEventIsFromDraggable,
        scale,
        structureNotSaved,
    } = props
    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id
    useBeforeunload(() => structureNotSaved && "You'll lose your data!")

    useEffect(() => {
        document.body.style.overflow = 'hidden'
        return () => {
            document.body.style.overflow = 'auto'
        }
    }, [])

    const handleMouseDown = e => {
        if (editorMode) {
            const path = e.nativeEvent.composedPath()
            for (let el of path) {
                if (el) {
                    if (
                        el.getAttribute('id') === 'panWrap' ||
                        el === document.body
                    ) {
                        setEventIsFromDraggable(false)
                        return
                    }
                    const classN = el.getAttribute('class')
                    if (
                        classN &&
                        (classN.indexOf('react-draggable') > -1 ||
                            classN.indexOf('tinyEditorText_') > -1)
                    ) {
                        setEventIsFromDraggable(true)
                        return
                    }
                }
            }
        }
        setEventIsFromDraggable(false)
    }

    const handleClick = e => {
        // if (!changeParentMode) {
        //     if (editorMode) {
        //         const path = e.nativeEvent.composedPath()
        //         for (let el of path) {
        //             if (el) {
        //                 if (
        //                     el.getAttribute('id') === 'panWrap' ||
        //                     el === document.body
        //                 ) {
        //                     // setChosenResource('')
        //                     setSelectedBox('')
        //                     return
        //                 }
        //                 const classN = el.getAttribute('class')
        //                 if (
        //                     classN &&
        //                     (classN.indexOf('react-draggable') > -1 ||
        //                         classN.indexOf('tinyEditorText_') > -1)
        //                 ) {
        //                     return
        //                 }
        //             }
        //         }
        //     }
        //     // setChosenResource('')
        //     setSelectedBox('')
        // }
    }

    return (
        <>
            <Prompt
                when={structureNotSaved}
                message="You have unsaved changes, are you sure you want to leave?"
            />
            <div
                style={{
                    display: 'flex',
                    width: '100%',
                    height: '100%',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        background: 'white',
                        zIndex: 1,
                        display: 'flex',
                    }}
                >
                    {isOwner ? (
                        <Button
                            onClick={() => {
                                setEditorMode(!editorMode)
                                setSelectedBox('')
                            }}
                            color="primary"
                        >
                            {editorMode ? <ArrowBack /> : t('g.edit')}
                        </Button>
                    ) : (
                        <Layout noContent />
                    )}
                    <div
                        style={{
                            flex: 1,
                            overflow: 'auto',
                            whiteSpace: 'nowrap',
                        }}
                    >
                        {isOwner && editorMode && structureNotSaved && (
                            <Button
                                onClick={saveStructure}
                                color="primary"
                                variant="contained"
                            >
                                {t('g.save')}
                            </Button>
                        )}
                    </div>
                </div>

                <div
                    style={{
                        overflow: 'hidden',
                        flex: 1,
                        display: 'flex',
                    }}
                    id="panWrap"
                    onMouseDown={handleMouseDown}
                    onTouchStart={handleMouseDown}
                    onTouchEnd={() => setEventIsFromDraggable(false)}
                    onMouseUp={() => setEventIsFromDraggable(false)}
                    onClick={handleClick}
                >
                    <TransformComponent>
                        <div
                            style={{
                                cursor: 'grabbing',
                            }}
                        >
                            <ContainerMM
                                id="b0"
                                setSelectedBox={setSelectedBox}
                                selectedBox={selectedBox}
                                scale={scale}
                                editorMode={editorMode}
                                changeParentMode={changeParentMode}
                                setChangeParentMode={setChangeParentMode}
                            />
                        </div>
                    </TransformComponent>
                </div>
            </div>

            <BottomMenu />
        </>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        structureNotSaved: state.structureNotSaved,
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveStructure: () => dispatch(actions.saveStructure()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(MindMap)
)
