import React, { useEffect, useRef } from 'react'

import Line from './Line'
import { Rnd } from 'react-rnd'
import { dragHandler, resizeHandler } from './actions'
import ChooseWrapMM from './ChooseWrapMM'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

export const resizeStyle = {
    height: '20px',
    width: '20px',
    background: '#eee',
    border: '1px #ccc solid',
    borderRadius: '10px',
    zIndex: 1,
}

const ContainerMM = props => {
    const myRef1 = useRef(null)

    const myRef4 = useRef(null)
    const {
        setSelectedBox,
        selectedBox,
        setSizepos,
        scale,
        myRef2,
        id,
        editingFolder,
        editingElement,
        selectedIndex,
        setSelectedIndex,
        setEditingElement,
        addDragStep,
        editorMode,
        setChosenResource,
        setEditingFolder,
        el,
        setChangeParentMode,
        changeParentMode,
        changeParentMM,
    } = props

    useEffect(() => {
        const handler = e => {
            // e.stopPropagation()
        }
        if (myRef4 && myRef4.current) {
            myRef4.current.addEventListener('touchstart', handler, {
                passive: false,
            })
            myRef4.current.addEventListener('touchmove', handler, {
                passive: false,
            })
            addDragStep()
        }

        return () => {
            if (myRef4 && myRef4.current) {
                myRef4.current.removeEventListener('touchstart', handler)
                myRef4.current.removeEventListener('touchmove', handler)
            }
        }
    }, [Boolean(myRef4 && myRef4.current)])
    return el &&
        !el.temp &&
        (!el.unsorted || (el.unsorted && el.children.length > 0)) ? (
        <div>
            {el.type !== 'profile' && (
                <Line start={myRef2} end={myRef1} collapsed={props.collapsed} />
            )}
            <div
                ref={myRef4}
                style={{
                    position: el.type === 'profile' ? 'relative' : 'absolute',
                }}
            >
                <Rnd
                    scale={scale}
                    default={el.type !== 'profile' && el.sizePos}
                    disableDragging={
                        el.type === 'profile' || editingFolder || !editorMode
                    }
                    enableResizing={el.type !== 'profile' && editorMode}
                    resizeHandleStyles={
                        selectedBox === id
                            ? {
                                  bottomLeft: resizeStyle,
                                  bottomRight: resizeStyle,
                                  topLeft: resizeStyle,
                                  topRight: resizeStyle,
                              }
                            : {}
                    }
                    onMouseDown={e => {
                        if (editorMode) {
                            if (changeParentMode) {
                                changeParentMM(changeParentMode, id)
                                setChangeParentMode('')
                            } else {
                                setSelectedBox(id)
                            }
                        }
                    }}
                    style={{
                        outline: selectedBox === id ? '1px solid blue' : 'none',
                        // overflow: 'hidden',
                    }}
                    onDrag={(e, data) => {
                        props.addDragStep()
                        dragHandler(id, data, setSizepos)
                    }}
                    onResize={(e, dir, elRef, delta, position) => {
                        props.addDragStep()
                        resizeHandler(id, elRef, position, setSizepos)
                    }}
                    cancel=".buttonsMM"
                >
                    <div
                        ref={myRef1}
                        style={{
                            width: '100%',
                            height: '100%',
                            display: 'flex',
                            position: 'relative',
                            background: 'white',
                        }}
                    >
                        {/* {el.children.length > 0 && (
                            <Button
                                style={{
                                    position: 'absolute',
                                    top: '-40px',
                                    right: '0px',
                                }}
                                variant="contained"
                            >{`${el.collapsed ? 'Expand' : 'Collapse'} ${
                                el.children.length
                            }`}</Button>
                        )} */}
                        <ChooseWrapMM
                            el={el}
                            id={id}
                            selectedBox={selectedBox}
                            scale={scale}
                            editingFolder={editingFolder}
                            editingElement={editingElement}
                            selectedIndex={selectedIndex}
                            setSelectedIndex={setSelectedIndex}
                            setEditingElement={setEditingElement}
                            setEditingFolder={setEditingFolder}
                            setChosenResource={setChosenResource}
                            editorMode={editorMode}
                            setChangeParentMode={setChangeParentMode}
                            changeParentMode={changeParentMode}
                        />
                    </div>
                </Rnd>

                <div
                    style={{
                        position: 'absolute',
                        // visibility:
                        //     props.collapsed || el.collapsed
                        //         ? 'hidden'
                        //         : 'visible',
                        transform:
                            'translate(' +
                            el.sizePos.x +
                            'px, ' +
                            el.sizePos.y +
                            'px)',
                    }}
                >
                    {el.children &&
                        el.children.map(itemId => (
                            <ConnectedContainerMM
                                key={itemId}
                                id={itemId}
                                setSelectedBox={setSelectedBox}
                                selectedBox={selectedBox}
                                scale={scale}
                                myRef2={myRef1}
                                collapsed={props.collapsed || el.collapsed}
                                editorMode={editorMode}
                                setChangeParentMode={setChangeParentMode}
                                changeParentMode={changeParentMode}
                            />
                        ))}
                </div>
            </div>
        </div>
    ) : null
}

const mapStateToProps = (state, ownProps) => {
    return {
        el: state.structureMM.items[ownProps.id],
        structure: state.structureMM,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
        setSizepos: (id, sizePos) => dispatch(actions.setSizepos(id, sizePos)),
        changeParentMM: (childId, parentId) =>
            dispatch(actions.changeParentMM(childId, parentId)),
    }
}

const ConnectedContainerMM = connect(
    mapStateToProps,
    mapDispatchToProps
)(ContainerMM)

export default ConnectedContainerMM
